@import "@/assets/global.scss";









































































































































































































































































.main-content {
  padding-top: 72px;
  padding-bottom: 140px;
  margin-top: 16px;
}

.banner-header {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.timer {
  color: #5C6AC4;
}

.info-panel {
  background: #FFFFFF;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.06);
  border-radius: 12px;
  padding: 12px;
  display: flex;
  justify-content: flex-start;

  > div {
    display: flex;
    flex-direction: column;
  }
}

.btn-panel {
  position: fixed;
  bottom: 0;
  background-color: #FAFAFA;
  width: 100%;
  height: 140px;
}

.btn-group {
  width: 300px;
  display: flex;
  justify-content: space-between;
  margin: 12px auto;

  > .btn {
    width: 145px;
    height: 46px;
    padding: 11px 0;
    background: #FFFFFF;
    border: 1px solid #E9E9E8;
    box-sizing: border-box;
    border-radius: 6px;
  }
}

.btn-confirm {
  width: 300px;
  height: 46px;
  padding: 11px 0;
  margin: 12px auto;
  box-sizing: border-box;
  background: #FCA311;
  border-radius: 6px;
}
